.PR_printUI .PR_printUI_Div .PR_printUI_Logos{
    display: grid;
    grid-template-columns: 33.33fr 33.33fr 33.33fr;
}
.PR_printUI .PR_printUI_Div .PR_printUI_Logos div{
    display: flex;
    justify-content: center;
    align-items: center;
}
.PR_printUI .PR_printUI_Div .PR_printUI_Logos img{
    width: 100%;
}
.PR_printUI .PR_printUI_Div .PR_printUI_Top{
    display: flex;
    justify-content: space-between;
}
.PR_printUI .PR_printUI_Div .PR_printUI_Middle{
    padding: 30px 0;
}
.PR_printUI .PR_printUI_Div .PR_printUI_Middle .PR_printUI_Grid {
    display: grid;
    grid-template-columns: 5fr 35fr 15fr 20fr 20fr;
}
.PR_printUI .PR_printUI_Div .PR_printUI_Middle .PR_printUI_Grid div{
    border: 1px solid rgb(201, 201, 201);
    text-align: center;
    padding: 5px 0;
}

.PR_printUI .PR_printUI_Div .PR_printUI_Bottom .PR_printUI_Grid1{
    display: grid;
    grid-template-columns: 10fr 90fr;
}
.PR_printUI .PR_printUI_Div .PR_printUI_Bottom .PR_printUI_Grid1 div{
    border: 1px solid rgb(201, 201, 201);
    text-align: center;
    padding: 5px 0;
}
.PR_printUI .PR_printUI_Div .PR_printUI_Bottom .PR_printUI_Remarks{
    padding: 20px 0;
}
.PR_printUI .PR_printUI_Div .PR_printUI_Bottom .PR_printUI_Remarks .DIVS_heading{
    border-bottom: 1px solid rgb(201, 201, 201);
    background-color: rgb(243, 243, 243);
    padding: 5px;
}
.PR_printUI .PR_printUI_Div .PR_printUI_Bottom .PR_printUI_Remarks .DIVS{
    display: flex;
    padding: 5px;
    border-bottom:  0 !important;
}

.PR_printUI .PR_printUI_Div .PR_printUI_Bottom .PR_printUI_Remark {
    border: 1px solid rgb(201, 201, 201);
    margin-bottom: 10px;
}

.PR_printUI .PR_printUI_Div .PR_printUI_Bottom .PR_printUI_Remark .DIVS {
    display: grid;
    grid-template-columns: 30fr 70fr;
}